<template>
  <div class="container">
    <div
      class="columns"
    >
      <div
        class="column"
      >
        <p v-if="daysBefore['100'] && daysBefore['100'].diff">
          {{ $tc('message.days', lifedays + daysBefore['100'].diff) }} {{ $t('message.before') }} {{ $tc('message.days', daysBefore['100'].diff) }} {{ daysBefore['100'].date }}
        </p>
        <p v-if="daysBefore['1000'] && daysBefore['1000'].diff">
          {{ $tc('message.days', lifedays + daysBefore['1000'].diff) }} {{ $t('message.before') }} {{ $tc('message.days', daysBefore['1000'].diff) }} {{ daysBefore['1000'].date }}
        </p>
      </div>
    </div>
    <div
      class="columns"
    >
      <div
        class="column"
      >
        <img
          :src="bioSrc"
          :alt="bioImage"
        >
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex';
  export default {
    name: 'BioChart',
    computed: {
      ...mapState({
        birthdate: state => state.birthdate,
        lifedays: state => state.lifedays,
        daysBefore: state => state.daysBefore,
        nameState: state => state.name,
      }),
      bioImage() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `${this.$t('message.bio_graph')} ${name} https://poplauki.eu/bio/`;
      },
      bioSrc() {
        return `https://poplauki.eu/bio/image3.php?year=${this.birthdate.year}&month=${this.birthdate.month}&day=${this.birthdate.day}`;
      }
    },
  }
</script>

