import Vue from 'vue'
import Vuex from 'vuex'
import { DateTime } from 'luxon'
import names from './names.json'
import aforizmy from './../data/aforizmy.json'

Vue.use(Vuex)

const shuffleArray = arr => arr
  .map(a => [Math.random(), a])
  .sort((a, b) => a[0] - b[0])
  .map(a => a[1]);

const takeDates = (days, div) => {
  const result = [];

  let i = days;
  while(result.length < 1) {
    if (i % div === 0) {
      result.push(i);
    }
    i++;
  }

  return result;
};

export default new Vuex.Store({
  state: {
    birthdate: null,
    lifedays: 0,
    lifeyears: 0,
    name: null,
    showNameForm: false,
    partdaylife: {
      sleep: 0,
      education: 0,
      work: 0,
      food: 0,
      free_time: 0,
      sport: 0,
      teeth: 0,
      green_light: 0,
      tears: 0,
      education_25: 0,
      work_63: 0,
      sport_63: 0
    },
    aforizm: {
      1: { id: '1', value: null },
      2: { id: '2', value: null },
      3: { id: '3', value: null }
    },
    aforizmChange: false,
    daysBefore: {
      100: {
        date: null,
        diff: 0,
      },
      1000: {
        date: null,
        diff: 0,
      }
    }
  },
  mutations: {
    setBirthdate (state, payload) {
      state.birthdate = null;
      state.lifedays = 0;
      state.lifeyears = 0;
      if (payload) {
        let value = `${payload}`;
        if (value.includes('GMT+')) {
          value = `${value.replace('GMT+', '+')}`;
        }

        if (value.includes('(') && value.includes(')')) {
          value = `${value.slice(0, value.indexOf('(')).trim()}`;
        }

        //console.dir(DateTime.fromFormatExplain(value, "EEE MMM dd yyyy TT ZZZ"));

        const bdt = DateTime.fromFormat(value, "EEE MMM dd yyyy TT ZZZ");
        state.birthdate = bdt;
        //console.dir(bdt, {depth: 2});
        const now = DateTime.local();
        if (bdt.isValid) {
          state.lifedays = Math.floor(now.diff(bdt).as('days'));
          state.lifeyears = Math.floor(now.diff(bdt).as('years'));

          // Life days stat
          if (state.lifeyears > 0 && state.lifeyears <= 25) {
            state.partdaylife.sleep = Math.floor(state.lifedays*18/48);
            state.partdaylife.education = Math.floor(state.lifedays*9/48);
            state.partdaylife.food = Math.floor(state.lifedays*5/48);
            state.partdaylife.free_time = Math.floor(state.lifedays*14/48);
            state.partdaylife.sport = Math.floor(state.lifedays*1/48);
            state.partdaylife.teeth = Math.floor(state.lifedays*15/9131);
            state.partdaylife.green_light = Math.floor(state.lifedays*61.25/9131);
            state.partdaylife.tears = Math.floor(state.lifedays*25/9131);
          } else if (state.lifeyears > 25 && state.lifeyears <= 63) {
            const daysYoung = state.lifedays - 9131;
            state.partdaylife.education_25 = Math.floor(9131 * 9 / 48);
            state.partdaylife.sleep = Math.floor(daysYoung * 16 / 48) + Math.floor(9131 * 18 / 48);
            state.partdaylife.work = Math.floor(daysYoung * 20 / 48);
            state.partdaylife.food = Math.floor(daysYoung * 5 / 48) + Math.floor(9131 * 5 / 48);
            state.partdaylife.free_time = Math.floor(daysYoung * 6 / 48) + Math.floor(9131 * 14 / 48);
            state.partdaylife.sport = Math.floor(daysYoung * 1 / 48) + Math.floor(9131 * 1 / 48);
            state.partdaylife.teeth = 15 + Math.floor(state.lifedays * 14 / state.lifedays);
            state.partdaylife.green_light = 61 + Math.floor(daysYoung * 64.25 / state.lifedays);
            state.partdaylife.tears = 25 + Math.floor(daysYoung * 40 / state.lifedays);
          } else {
            const daysChild = 9131;
            const daysYoung = 23058;
            const daysAdult = state.lifedays - daysYoung;
            const fulldays = 36526;

            state.partdaylife.education_25 = Math.floor(daysChild * 9 / 48);
            state.partdaylife.sport_63 = Math.floor(daysChild * 1 / 48) + Math.floor(daysAdult * 1 / 48) + Math.floor(daysYoung * 1 / 48);

            state.partdaylife.sleep = Math.floor(daysYoung * 16 / 48) + Math.floor(daysAdult * 18 / 48) + Math.floor(daysYoung * 16 / 48) ;
            state.partdaylife.work = Math.floor(daysAdult * 20 / 48);
            state.partdaylife.food = Math.floor(daysYoung * 5 / 48) + Math.floor(daysAdult * 5 / 48) + Math.floor(daysYoung * 5 / 48);
            state.partdaylife.free_time = Math.floor(daysYoung * 6 / 48) +  Math.floor(daysAdult * 6 / 48) + Math.floor(9131 * 14 / 48);

            state.partdaylife.teeth = 29 + Math.floor(state.lifedays * 7 / fulldays);
            state.partdaylife.green_light = 66 + Math.floor(daysYoung * 57 / fulldays);
            state.partdaylife.tears = 65 + Math.floor(daysYoung * 5 / fulldays);
          }

          // Round dates
          if (!state.daysBefore['100'].diff) {
            const hungreds = takeDates(state.lifedays, 100);
            state.daysBefore['100'].diff = hungreds[0] - state.lifedays;
            state.daysBefore['100'].date = DateTime.local().plus({days: state.daysBefore['100'].diff}).toLocaleString();
          }

          if (!state.daysBefore['1000'].diff) {
            const thousands = takeDates(state.lifedays, 1000);
            state.daysBefore['1000'].diff = thousands[0] - state.lifedays;
            state.daysBefore['1000'].date = DateTime.local().plus({days: state.daysBefore['1000'].diff}).toLocaleString();
          }
        }
      }
    },
    setName (state, payload) {
      let id = null;
      let description = null;
      if (names) {
        names.forEach((item, index) => {
          if (item.name === payload) {
            id = item.id;
            description = item.description;
          }
        });
      }
      state.name = { value: payload, id, description };
      state.showNameForm = false;
    },
    setShowNameForm (state) {
      state.showNameForm = !state.showNameForm;
    },
    setAforizms (state, payload) {
      try {
        const data = JSON.parse(payload);
        const dt = DateTime.fromISO(data.date);
        const now = DateTime.local();
        const difff = Math.floor(now.diff(dt).as('days'));
        const count = (aforizmy) ? aforizmy.length : 0;

        if (count) {

          if (difff > 0 || !dt.isValid) {
            const list = shuffleArray(aforizmy);
            state.aforizm['1'] = {...list[0]};
            state.aforizm['2'] = {...list[1]};
            state.aforizm['3'] = {...list[2]};
            state.aforizmChange = true;
          } else {
            state.aforizmChange = false;
            if ((!state.aforizm['1'].value || !state.aforizm['2'].value || !state.aforizm['3'].value) && Array.isArray(data.keys)) {
              const list = aforizmy.filter(item => data.keys.includes(item.id));
              state.aforizm['1'] = {...list[0]};
              state.aforizm['2'] = {...list[1]};
              state.aforizm['3'] = {...list[2]};
            }
          }
        }
      } catch (e) {
        console.error(e);
      }
    },
  },
  actions: {
    updateBirthdate({ commit }, data) {
      commit('setBirthdate', data);
    },
    updateName({ commit }, data) {
      commit('setName', data);
    },
    updateShowNameForm({ commit }) {
      commit('setShowNameForm');
    },
    updateAforizms({ commit }, data) {
      commit('setAforizms', data);
    },
  },
  modules: {
  }
})
