<template>
  <div
    class="form"
  >
    <div
      v-if="!name || showForm"
      class="field is-horizontal"
    >
      <div class="field-body">
        <div class="field is-grouped">
          <p class="control is-expanded">
            <input
              id="data-inputName"
              v-model="inputName"
              :class="{ 'input': true, 'is-danger': errors['name'], 'is-light': !errors['name'] }"
              type="text"
              name="data-inputName"
              :placeholder="$t('message.input_name')"
              autocomplete="off"
            >
          </p>
        </div>
      </div>
    </div>
    <div
      v-if="!name || showForm"
      class="field is-horizontal"
    >
      <div class="field-body">
        <p class="control">
          <button
            class="button is-info"
            :disabled="!errors['name'] ? false: 'disabled'"
            @click="sendName"
          >
            {{ $t("message.set") }}
          </button>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapState} from "vuex";

  export default {
    name: 'NameInput',
    props: {
      showForm: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        errors: {
          'name': null
        },
        inputName: '',
      }
    },
    computed: {
      ...mapState({
        nameState: state => state.name,
      }),
      formtedBirthdate() {
        return this.userBirthDate.toLocaleDateString();
      },
      name() {
        return (this.nameState && this.nameState.value) ? this.nameState.value : '';
      }
    },
    methods: {
      ...mapActions({
        updateName: 'updateName',
      }),
      sendName() {
        this.errors['name'] = null;
        if (this.inputName) {
          this.updateName(this.inputName);
          localStorage.setItem(this.$t('message.cookie_name_key'), this.inputName);
        } else {
          this.errors['name'] = `${this.$t('message.name_help')}`;
        }
      }
    }
  }
</script>

