<template>
  <div
    class="form"
  >
    <div
      v-if="!birthdate"
      class="field is-horizontal"
    >
      <div class="field-body">
        <div class="field is-grouped">
          <p class="control is-expanded">
            <vc-date-picker
              v-model="selectedDate"
              color="red"
              is-blue
              is-inline
              :input-props="{
                class: &quot;input is-light&quot;,
                placeholder: `${$t(&quot;message.day_of_birth&quot;)}`,
                readonly: true
              }"
              :first-day-of-week="1"
              :masks="{ title: 'MMM YYYY', data: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'] }"
            />
          </p>
        </div>
      </div>
    </div>
    <div
      v-if="!birthdate"
      class="field is-horizontal"
    >
      <div class="field-body">
        <p class="control">
          <button
            class="button is-info"
            :disabled="!errors['selectedDate'] ? false: 'disabled'"
            @click="sendBirthdate"
          >
            {{ $t("message.calculate") }}
          </button>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'BirthdateInput',
    data () {
      return {
        errors: {
          'selectedDate': null
        },
        selectedDate: new Date(),
      }
    },
    computed: {
      ...mapState({
        birthdate: state => state.birthdate,
      }),
      formtedBirthdate() {
        return this.userBirthDate.toLocaleDateString();
      }
    },
    watch: {
      selectedDate(to, from) {
        if (to) {
          this.updateBirthdate(to);
          localStorage.setItem(this.$t('message.cookie_key'), to);
        }
      },
    },
    methods: {
      ...mapActions({
        updateBirthdate: 'updateBirthdate',
      }),
      sendBirthdate() {
        this.updateBirthdate(this.selectedDate);
        localStorage.setItem(this.$t('message.cookie_key'), this.selectedDate);
      }
    }
  }
</script>

