<template>
  <div
    id="app"
    class="app"
  >
    <nav
      class="navbar"
      role="navigation"
      aria-label="main navigation"
    >
      <div class="navbar-brand">
        <router-link
          to="/"
          class="navbar-item is-active"
        >
          {{ $t("message.home") }}
        </router-link>
        <a
          role="button"
          class="navbar-burger"
          data-target="navMenu"
          aria-label="menu"
          aria-expanded="false"
          @click="openNavigation"
        >
          <span aria-hidden="true" />
          <span aria-hidden="true" />
          <span aria-hidden="true" />
        </a>
      </div>
      <div :class="{'navbar-menu': true, 'is-active': navigationMain }">
        <div class="navbar-start">
          <div class="navbar-item has-dropdown is-hoverable">
            <a
              v-if="$i18n.locale === 'en'"
              class="navbar-link"
              :style="{'width':`120px`}"
            >
              <flagIcon
                :name="'gb'"
              />
            </a>
            <a
              v-if="$i18n.locale === 'lt'"
              class="navbar-link"
              :style="{'width':`120px`}"
            >
              <flagIcon
                :name="'lt'"
              />
            </a>
            <a
              v-if="$i18n.locale === 'ru'"
              class="navbar-link"
              :style="{'width':`120px`}"
            >
              <flagIcon
                :name="'ru'"
              />
            </a>
            <a
              v-if="$i18n.locale === 'pl'"
              class="navbar-link"
              :style="{'width':`120px`}"
            >
              <flagIcon
                :name="'pl'"
              />
            </a>
            <div class="navbar-dropdown">
              <a
                v-if="$i18n.locale !== 'en'"
                class="navbar-item"
                href="#"
                @click="selectLang('en')"
              >
                <flagIcon
                  :name="'gb'"
                />
              </a>
              <a
                v-if="$i18n.locale !== 'lt'"
                class="navbar-item"
                href="#"
                @click="selectLang('lt')"
              >
                <flagIcon
                  :name="'lt'"
                />
              </a>
              <a
                v-if="$i18n.locale !== 'ru'"
                class="navbar-item"
                href="#"
                @click="selectLang('ru')"
              >
                <flagIcon
                  :name="'ru'"
                />
              </a>
              <a
                v-if="$i18n.locale !== 'pl'"
                class="navbar-item"
                href="#"
                @click="selectLang('pl')"
              >
                <flagIcon
                  :name="'pl'"
                />
              </a>
            </div>
          </div>
        </div>
        <div class="navbar-end">
          <div class="buttons">
            <button
              class="button is-info"
              @click="addName"
            >
              {{ $t('message.input_name') }}
            </button>
            <button
              v-if="birthdate"
              class="button is-info"
              @click="resetBirthdate"
            >
              {{ $t('message.new_birthdate') }}
            </button>
          </div>
        </div>
      </div>
    </nav>
    <router-view />
    <footer class="footer">
      <div class="content has-text-centered">
        <p>
          <strong>Poplauki.eu</strong> by <a href="https://poplauki.eu">Poplavskij Vladimir</a>.
        </p>
      </div>
    </footer>
  </div>
</template>

<script>
  import {DateTime} from 'luxon';
  import { mapActions, mapState } from 'vuex';
  import flagIcon from '@/components/flagIcon.vue';

  export const storageAvailable = (type) => {
    let storage = false;
    try {
      storage = window[type];
      var x = '__storage_test__';
      storage.setItem(x, x);
      storage.removeItem(x);
      return true;
    } catch(e) {
      return e instanceof DOMException && (
          // everything except Firefox
        e.code === 22 ||
        // Firefox
        e.code === 1014 ||
        // test name field too, because code might not be present
        // everything except Firefox
        e.name === 'QuotaExceededError' ||
        // Firefox
        e.name === 'NS_ERROR_DOM_QUOTA_REACHED') &&
        // acknowledge QuotaExceededError only if there's something already stored
        (storage && storage.length !== 0);
    }
  };

  export default {
    name: 'App',
    components: {
      flagIcon
    },
    data () {
      return {
        navigationMain: false,
      }
    },
    computed: {
      ...mapState({
        birthdate: state => state.birthdate,
        aforizmChange: state => state.aforizmChange,
        aforizm: state => state.aforizm,
      })
    },
    watch: {
      $route(to, from) {
        this.navigationMain = false;
      }
    },
    created () {
      if (storageAvailable('localStorage')) {
        if(localStorage.getItem(this.$t('message.cookie_key'))) {
          this.updateBirthdate(localStorage.getItem(this.$t('message.cookie_key')));
        }

        if(localStorage.getItem(this.$t('message.cookie_name_key'))) {
          this.updateName(localStorage.getItem(this.$t('message.cookie_name_key')));
        }

        if(localStorage.getItem(this.$t('message.cookie_aforizms_key'))) {
          this.updateAforizms(localStorage.getItem(this.$t('message.cookie_aforizms_key')));
        } else {
          this.updateAforizms('{"date":null}');
        }

        if (this.aforizmChange) {
          const data = {
            date: DateTime.local().toISO(),
            keys: [ this.aforizm['1'].id, this.aforizm['2'].id, this.aforizm['3'].id ],
          };
          localStorage.setItem(this.$t('message.cookie_aforizms_key'), JSON.stringify(data));
        }

        if(localStorage.getItem(this.$t('message.cookie_lang_key'))) {
          this.$i18n.locale = localStorage.getItem(this.$t('message.cookie_lang_key'));
        }
        //console.dir(localStorage, {depth: 2});
      }
    },
    methods: {
      ...mapActions({
        updateBirthdate: 'updateBirthdate',
        updateName: 'updateName',
        updateShowNameForm: 'updateShowNameForm',
        updateAforizms: 'updateAforizms'
      }),
      selectLang(lang) {
        this.$i18n.locale = lang;
        localStorage.setItem(this.$t('message.cookie_lang_key'), lang);
      },
      openNavigation() {
        this.navigationMain = !this.navigationMain;
      },
      resetBirthdate() {
        this.updateBirthdate(null);
        if (localStorage.getItem(this.$t('message.cookie_key'))) {
          localStorage.removeItem(this.$t('message.cookie_key'));
        }
      },
      addName() {
        this.updateShowNameForm();
      }
    }
  }
</script>


