<script>
  import { mapActions, mapState } from 'vuex';
  import {DateTime} from 'luxon';
  import cisladaty from './../data/cisladaty.json';

  export const sumNumber = value => {
    const str = `${value}`;
    const sum = str.split('')
      .map(item => Number(item))
      .reduce((accumulator, currentValue) => accumulator + currentValue);
    return sum;
  };

  export default {
    name: 'InfoBlocks',
    data () {
      return {
        hosting: 'poplauki.eu/bio_vue',
        activeItems: [],
        bannerSelected: 1
      }
    },
    computed: {
      ...mapState({
        birthdate: state => state.birthdate,
        nameState: state => state.name,
        lifedays: state => state.lifedays,
        lifeyears: state => state.lifeyears,
        lifeyears: state => state.lifeyears,
        partdaylife: state => state.partdaylife,
        aforizm: state => state.aforizm,
      }),
      url () {
        return `https://${this.hosting}/${this.urlParams}`.trim();
      },
      urlParams() {
        const name = (this.nameState && this.nameState.id) ? `&id=${this.nameState.id}` : '';
        return `year=${this.birthdate.year}&month=${this.birthdate.month}&day=${this.birthdate.day}${name}`;
      },
      name() {
        return (this.nameState && this.nameState.value) ? this.nameState.value : '';
      },
      nameDescr() {
        return (this.nameState && this.nameState.description) ? this.nameState.description : '';
      },
      birthNumber() {
        let sum = sumNumber(`${this.birthdate.year}${this.birthdate.month}${this.birthdate.day}`);
        while (sum >= 10) {
          sum = sumNumber(sum);
        }
        return sum;
      },
      bioBanner1Src() {
        return `https://poplauki.eu/bio/banner1.php?y=${this.birthdate.year}&m=${this.birthdate.month}&d=${this.birthdate.day}&lng=${this.$i18n.locale}`;
      },
      bioBanner1Alt() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `${this.$t('message.bannerdescr')} ${name} https://poplauki.eu/bio/`;
      },
      bioBanner1Image() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `<img src="${this.bioBanner1Src}" alt="${this.bioBanner1Alt}" style="border:none;">`;
      },
      bioBanner1Code() {
        return `<a href="https://popaluki.eu/bio/" target="_blank" style="border:none;"><img src="${this.bioBanner1Src}" alt="'${this.bioBanner1Alt}" style="border:none;"></a>`;
      },
      bioBanner2Src() {
        return `https://poplauki.eu/bio/banner1.php?y=${this.birthdate.year}&m=${this.birthdate.month}&d=${this.birthdate.day}&lng=${this.$i18n.locale}&r=1`;
      },
      bioBanner2Alt() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `${this.$t('message.bannerdescr')} ${name} https://poplauki.eu/bio/`;
      },
      bioBanner2Image() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `<img src="${this.bioBanner2Src}&r=1" alt="${this.bioBanner2Alt}" style="border:none;">`;
      },
      bioBanner2Code() {
        return `<a href="https://popaluki.eu/bio/" target="_blank" style="border:none;"><img src="${this.bioBanner2Src}&r=1" alt="'${this.bioBanner2Alt}" style="border:none;"></a>`;
      },
      bioBanner3Src() {
        return `https://poplauki.eu/bio/banner2.php?y=${this.birthdate.year}&m=${this.birthdate.month}&d=${this.birthdate.day}&lng=${this.$i18n.locale}`;
      },
      bioBanner3Alt() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `${this.$t('message.bannerdescr2')} ${name} https://poplauki.eu/bio/`;
      },
      bioBanner3Image() {
        const name = (this.nameState && this.nameState.value) ? `${this.nameState.value}` : '';
        return `<img src="${this.bioBanner3Src}" alt="${this.bioBanner3Alt}" style="border:none;">`;
      },
      bioBanner3Code() {
        return `<a href="https://popaluki.eu/bio/" target="_blank" style="border:none;"><img src="${this.bioBanner3Src}&r=1" alt="'${this.bioBanner3Alt}" style="border:none;"></a>`;
      }
    },
    methods: {
      changeActive(item) {
        if (this.activeItems.includes(item)) {
          this.activeItems = [...this.activeItems.filter(menuItem => menuItem !== item)];
        } else {
          this.activeItems.push(item);
        }
      },
      getBirthDateDescription(number) {
        let result = '';
        if (cisladaty) {
          const filtered = cisladaty.filter(item => item.id === number);
          if (filtered[0]) {
            result = filtered[0].description;
          }
        }
        return result;
      },
      changeActiveBanner(num) {
        this.bannerSelected = num;
      }
    }
  }
</script>

<style lang="scss">
  .arrow {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
  }

  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

  .up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
  }

  .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  .code-pre {
    max-width: 380px;
    height: 115px;
    overflow: auto;
    text-align: left;
  }

  pre {
    white-space: pre-wrap !important;       /* css-3 */
    white-space: -moz-pre-wrap !important;  /* Mozilla, since 1999 */
    white-space: -pre-wrap !important;      /* Opera 4-6 */
    white-space: -o-pre-wrap !important;    /* Opera 7 */
    word-wrap: break-word !important;
  }
</style>
